body {
  background-color: #f2f2f2;
}

div.welcomeMessage h1 {
  /* Layout Properties */
  padding-left: 5%;
  padding-bottom: 30px;

  /* Content UI */
  color: #3e2e2e;
  font-family: "Open Sans", sans-serif;
  font-size: 32px;
  font-weight: bold;
  line-height: 48px;
}

div.welcomeMessage h1 {
  /* Layout Properties */
  padding-left: 5%;
  padding-top: 30px;

  /* Content UI */
  color: #3e2e2e;
  font-family: "Open Sans";
  font-size: 24px;
  font-weight: bold;
  line-height: 36px;
}


div.welcomeMessage h1 {
  position: relative;
  top: 65px;
  margin-bottom: 50px;
}

.heading {
  margin-bottom: 10px;
}

.dateTime {
  /* Layout Properties */
  position: absolute;
  top: 100px;
  right: 5%;
  margin-left: 700px;

  color: #878b8e;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 24px;
}

.p {
  color: #3e2e2e;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 24px;
}

.recentUpdates {
  display: inline-block;
  padding-left: 5%;
}

.card {
  /* Layout Properties */
  min-width: 560px;
  width: 560px;
  height: 60px;
  margin-bottom: 10px;

  /* UI Properties */
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 2px 6px #00000029;
  opacity: 1;

  /* Text Properties */
  text-align: left;
  padding: 18px 10px;
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}

div.recentUpdates h4,
div.coursePreview h4,
div.resourceSection h4 {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  color: #0076a3;
  line-height: 36px;
  font-size: 24px;
}

.coursePreview {
  padding: 100px;
  padding-left: 5%;
  padding-top: 20px;
  padding-bottom: 20px;
}

.cardL {
  /* Layout Properties */
  max-width: 550px;
  width: 550px;
  min-height: 100px;
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 20px;

  /* UI Properties */
  background: #0076a3 0% 0% no-repeat padding-box;
  box-shadow: 0px 2px 6px #00000029;
  opacity: 1;

  /* Text Properties */
  color: #ffffff;
  text-align: left;
  padding: 18px 10px;
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}

.courseTitle {
  font-size: 14px;
  line-height: 21px;
  font-family: "Roboto", sans-serif;
  font-weight: oblique;
  padding-left: 20px;
  text-transform: uppercase;
}

div.coursePreview a {
  text-align: left;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  line-height: 24px;
  font-weight: bold;
  padding-left: 20px;
}

a.time {
  text-align: left;
  font-family: "Roboto", sans-serif;
  float: right;
}

a.days {
  text-align: left;
  font-family: "Roboto", sans-serif;
  float: right;
}

p.subtext {
  text-align: left;
  font-size: 16px;
  font-family: "Roboto", sans-serif;
  line-height: 24px;
  padding-left: 20px;
}

.resourceSection {
  padding-left: 5%;
  padding-top: 20px;
  padding-bottom: 20px;
}

.bstyle {
  /* Layout Properties */
  max-width: 550px;
  width: 550px;
  min-height: 100px;
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 20px;

  /* UI Properties */
  background: #0076a3 0% 0% no-repeat padding-box;
  box-shadow: 0px 2px 6px #00000029;
  opacity: 1;

  /* Text Properties */
  color: #ffffff;
  text-align: left;
  padding: 18px 10px;
  font-family: "Roboto", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
}

.bstyle.button {
  outline: none;
  border: none;
}

.bstyle.button:hover {
  background-color: lightgrey;
}

.btext {
  text-align: left;
  font-size: 14px;
  font-family: "Roboto", sans-serif;
  line-height: 21px;
  padding-left: 20px;
}

.btitle {
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", sans-serif;
  font-weight: oblique;
  padding-left: 20px;
  text-transform: uppercase;
}

.blue {
  background: #63baf5;
}

.purple {
  background: #bf89fe;
}

.green {
  background: #75d862;
}

.orange {
  background: #fb8540;
}
